.links, .btn, .copyright, .label, .description, .input, .descriptor, .markdown p {
  font-family: menco, sans-serif;
  font-weight: 500;
  font-style: normal; }

.display, .sectionheader, .markdown h2, .markdown h3, .markdown h4, .markdown h5, .markdown h6 {
  font-family: menco,sans-serif;
  font-weight: 700;
  font-style: normal; }

.basedemofont {
  font-family: base-demofont, sans-serif; }

.demofont {
  font-family: demofont, sans-serif; }

.display {
  text-align: center; }

.links {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
  letter-spacing: 0.02rem; }

.btn {
  font-size: 0.9rem;
  letter-spacing: 0.02rem; }

.icon {
  width: 3em;
  pointer-events: none;
  text-align: center;
  display: block;
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%; }

.copyright {
  margin-bottom: 1em;
  font-size: 0.9rem; }

.small {
  font-size: 0.9em;
  opacity: 0.6; }

.label {
  font-size: 0.8em;
  display: block;
  margin-bottom: 0.5em; }

.sectionheader {
  font-size: 1em; }

.description {
  font-size: 0.8em; }

.input {
  font-size: 1em; }

.descriptor {
  text-align: center;
  font-size: 0.8em;
  opacity: 0.6; }

.markdown p {
  line-height: 1.5em; }
