.example {
  font-size: 52pt;
  font-family: demofont, sans-serif;
  height: 12em;
  width: 100%; }

.text {
  font-size: 2em; }

.root {
  font-family: sans-serif;
  font-size: 16pt;
  margin: auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  box-sizing: border-box; }

.actions {
  display: flex;
  justify-content: flex-end;
  align-self: flex-end;
  padding: 0.5em; }

.workspace {
  height: 100%;
  flex-grow: 1; }

.panel, .textpanel {
  padding: 1em;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  page-break-after: always;
  box-sizing: border-box; }

.textpanel {
  height: 100%; }

.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  box-sizing: border-box;
  flex-grow: 1; }

.topaction {
  max-width: 500px;
  padding: 1em;
  margin: auto;
  width: 100%;
  box-sizing: border-box; }

.internalpanel {
  display: flex;
  flex-direction: column;
  height: 100%; }

@media print {
  .panel {
    height: auto; } }

@media screen and (min-width: 1000px) {
  .workspace {
    display: flex; }
  .panel {
    padding: 1em;
    width: 50%;
    max-width: 50%;
    flex-shrink: 0;
    box-sizing: border-box; } }

html, body {
  height: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 16pt;
  min-width: 350px; }
